import Vue from "vue";
import store from "@/store";
import axios from "axios";

Vue.prototype.$version = "4.8.8";

/*第一层if判断生产环境和开发环境*/
if (process.env.NODE_ENV === "production") {
  /*第二层if，根据.env文件中的VUE_APP_FLAG判断是生产环境还是测试环境*/
  if (process.env.VUE_APP_FLAG === "prod") {
    //production 生产环境
    Vue.prototype.$baseUrl = "http://3.1.240.129:8386/";
  } else {
    //test 测试环境
    Vue.prototype.$baseUrl = "http://3.1.240.129:8386/";
  }
} else {
  // dev 开发环境
  Vue.prototype.$baseUrl = "http://3.1.240.129:8386/";
  // Vue.prototype.$baseUrl = "/api/"; //调本地接口
}

const instance = axios.create({
  baseURL: Vue.prototype.$baseUrl,
});

instance.interceptors.request.use(
  (config) => {
    config.headers.token = store.getters.token;
    config.headers.Version = Vue.prototype.$version;
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

instance.interceptors.response.use(
  function (response) {
    // 处理响应数据
    if (parseInt(response.data.code) !== 0) {
      //   if (
      //     (parseInt(response.data.code) === 4000 &&
      //       response.data.msg === "用户不存在") ||
      //     parseInt(response.data.code) === 4001 ||
      //     (parseInt(response.data.code) === 4003 &&
      //       response.data.msg == "当前账户已被禁用,请联系管理员")
      //   ) {
      //     Vue.prototype.$alert(
      //       (response.data.msg || "登录信息已过期") + "，请重新登录",
      //       "温馨提示",
      //       {
      //         type: "warning",
      //         showClose: false,
      //         center: true,
      //         confirmButtonText: "确定",
      //         callback: () => {
      //           store.dispatch("user/logout");
      //         },
      //       }
      //     );
      //     return;
      //   }
      return Promise.reject(response);
    } else {
      return response.data;
    }
  },
  function (error) {
    // 处理响应失败
    return Promise.reject(error);
  }
);

Vue.prototype.$axios = instance;

export default instance;
